.header-example {
  background-color: #ffffff;
  padding: 20px 0;
  transition: transform 0.3s ease, background-color 0.3s ease;
  z-index: 30;
}
.header-example--sticky {
  left: 0;
  position: fixed;
  top: 0;
  width: 100%;
}
.header-example--base {
  background-color: #ffffff;
}
.header-example--blue {
  background-color: #3c69ff;
}
.header-example--red {
  background-color: #f03838;
}
.header-example--green {
  background-color: #17ff45;
}
.header-example--yellow {
  background-color: #f4ff25;
}
.header-example.is-hidden {
  transform: translateY(-100%);
}
.header-example .container {
  align-items: center;
  display: flex;
  justify-content: space-between;
}
.header-example__logo {
  display: flex;
  margin: -8px 40px 0 0;
}
@media (max-width: 1279px) {
  .header-example__logo {
    flex-direction: column;
    transform: scale(0.85);
  }
}
@media (max-width: 1023px) {
  .header-example__logo {
    flex-direction: row;
    transform: scale(1);
  }
}
.is-open .header-example__logo {
  position: relative;
  z-index: 25;
}
@media (max-width: 1279px) {
  .header-example__logo .logo__second-part {
    margin: 0;
  }
}
@media (max-width: 1023px) {
  .header-example__logo .logo__second-part {
    margin: 0 0 0 9px;
  }
}
@media (max-width: 767px) {
  .header-example__logo .logo__second-part {
    margin: 0;
  }
}
.header-example__git-link {
  display: flex;
  margin: 0 0 0 auto;
}
@media (max-width: 1023px) {
  .header-example__git-link {
    margin: 0 24px 0 auto;
  }
}
@media (max-width: 1023px) {
  .header-example__nav {
    background-color: #2c39f2;
    bottom: 0;
    display: flex;
    flex-direction: column;
    left: 0;
    margin: 0;
    opacity: 0;
    padding: 123px 20px 20px 20px;
    pointer-events: none;
    position: fixed;
    right: 0;
    top: 0;
    transition: all 0.3s ease;
    visibility: hidden;
    z-index: 20;
  }
}
.header-example--blue .header-example__nav {
  background-color: #3c69ff;
}
.header-example--red .header-example__nav {
  background-color: #f03838;
}
.header-example--green .header-example__nav {
  background-color: #17ff45;
}
.header-example--yellow .header-example__nav {
  background-color: #f4ff25;
}
.is-open .header-example__nav {
  opacity: 1;
  pointer-events: all;
  visibility: visible;
}
.is-open .header-example__nav .header-example__item {
  opacity: 1;
  transform: translateX(0);
}
.header-example__list {
  align-items: center;
  display: flex;
  flex-wrap: wrap;
  list-style: none;
  margin: 0;
  max-width: 450px;
  padding: 0;
}
@media (max-width: 1023px) {
  .header-example__list {
    flex-direction: column;
    margin: 0 0 70px 0;
    max-width: unset;
  }
}
@media (max-width: 767px) {
  .header-example__list {
    align-items: flex-start;
    margin: 0 0 62px 0;
  }
}
@media (max-width: 1023px) {
  .header-example__item {
    opacity: 0;
    transform: translateX(-15px);
    transition: all ease-out 0.15s;
  }
}
.header-example__item:not(:last-child) {
  margin: 0 55px 0 0;
}
@media (max-width: 1279px) {
  .header-example__item:not(:last-child) {
    margin: 0 35px 0 0;
  }
}
@media (max-width: 1023px) {
  .header-example__item:not(:last-child) {
    margin: 0 0 40px 0;
  }
}
@media (max-width: 767px) {
  .header-example__item:not(:last-child) {
    margin: 0 0 36px 0;
  }
}
.header-example__link {
  font-size: 16px;
  font-weight: 600;
  line-height: 25px;
  transition: color 0.3s ease;
}
@media (max-width: 1023px) {
  .header-example__link {
    color: #ffffff;
    font-size: 20px;
    line-height: 25px;
  }
}
@media (hover: hover) {
  .header-example__link:hover:not(.focus-visible):not(span) {
    color: #2c39f2;
  }
}
.header-example__link--blue {
  color: #2c39f2;
}
@media (hover: hover) {
  .header-example__link--blue:hover:not(.focus-visible):not(span) {
    color: #ff1553;
  }
}
.header-example__burger {
  align-items: center;
  background-color: transparent;
  border: none;
  color: #000000;
  display: none;
  padding: 0;
  position: relative;
  z-index: 25;
}
@media (max-width: 1023px) {
  .header-example__burger {
    display: flex;
    position: relative;
    transition: all 0.3s ease;
  }
}
.header-example__burger .is-open .header-example__burger-text {
  opacity: 0;
  pointer-events: none;
  transition: opacity 0.3s ease 0s;
}
.header-example__burger .is-open .header-example__burger-icon {
  opacity: 1;
  pointer-events: all;
  transition: opacity 0.3s ease 0.2s;
}
.header-example__burger-text {
  cursor: pointer;
  font-family: "Neue Machina", "Arial", sans-serif;
  font-size: 18px;
  font-weight: 700;
  line-height: 1;
  transition: opacity 0.3s ease 0.1s;
}
.is-open .header-example__burger-text {
  opacity: 0;
  pointer-events: none;
  transition: opacity 0.3s ease 0s;
}
.header-example__burger-icon {
  cursor: pointer;
  opacity: 0;
  pointer-events: none;
  position: absolute;
  right: 0;
  stroke: #ffffff;
  top: 50%;
  transform: translateY(-50%);
  transition: opacity 0.3s ease;
}
.is-open .header-example__burger-icon {
  opacity: 1;
  pointer-events: all;
  transition: opacity 0.3s ease 0.2s;
}

.section-example {
  height: 100vh;
}
.section-example:nth-child(2) {
  background-color: #f03838;
}
.section-example:nth-child(3) {
  background-color: #3c69ff;
}
.section-example:nth-child(5) {
  background-color: #f4ff25;
}
.section-example:nth-child(6) {
  background-color: #17ff45;
}

.ytdefer {
  height: 180px;
  margin: 0 auto;
  width: 320px;
}

.ytdefer > div,
.ytdefer > iframe {
  height: inherit;
  width: inherit;
}

.ytdefer > div {
  align-items: center;
  display: flex;
  justify-content: center;
  position: absolute;
}

.ytdefer > div > button {
  cursor: pointer;
  height: 50px;
  width: 50px;
  z-index: 1;
}

[data-tabs=parent] {
  overflow: hidden;
}
[data-tabs=parent].no-transition > [data-tabs=control],
[data-tabs=parent].no-transition > [data-tabs=content],
[data-tabs=parent].no-transition > [data-tabs=element] {
  transition: none;
}
[data-tabs=parent].no-transition-global [data-tabs=control],
[data-tabs=parent].no-transition-global [data-tabs=content],
[data-tabs=parent].no-transition-global [data-tabs=element] {
  transition: none;
}

[data-tabs=parent] [data-tabs=content] {
  position: relative;
}

[data-tabs=element] {
  position: absolute;
  visibility: hidden;
  opacity: 0;
  transition: opacity 0.3s ease-in, visibility 0.3s ease-in;
}

[data-tabs=element].for-load {
  position: static;
  visibility: visible;
  opacity: 1;
  transition: none;
}

[data-tabs=element].is-active {
  z-index: 1;
  visibility: visible;
  opacity: 1;
}

.tabs {
  max-width: 100%;
}

.tabs__controls {
  display: flex;
  justify-content: space-between;
  margin-bottom: 40px;
  width: 100%;
}
@media screen and (max-width: 767px) {
  .tabs__controls {
    flex-wrap: wrap;
  }
}

.tabs__control {
  cursor: pointer;
  justify-content: center;
  margin: 0 20px;
  min-width: auto;
  opacity: 0.5;
  text-transform: uppercase;
  width: 25%;
}
@media screen and (max-width: 1023px) {
  .tabs__control {
    margin: 0 10px;
    padding: 15px;
  }
}
@media screen and (max-width: 767px) {
  .tabs__control {
    margin: 10px 2% 0;
    padding: 10px;
    width: 45%;
  }
}
.tabs__control.is-active {
  opacity: 1;
  pointer-events: none;
}

.tabs__content {
  transition: height 0.3s;
}

.tabs__element {
  width: 100%;
}
.tabs__element:nth-child(1) {
  background-color: rgb(250, 235, 215);
}
.tabs__element:nth-child(2) {
  background-color: rgb(229, 250, 215);
}
.tabs__element:nth-child(3) {
  background-color: rgb(180, 188, 233);
}
.tabs__element:nth-child(4) {
  background-color: rgb(255, 187, 204);
}

.tabs--unset .tabs__content {
  height: 100px;
}

.tabs--unset .tabs__element {
  height: 100%;
  overflow-x: hidden;
  overflow-y: auto;
}

