.form {
  background-color: #102654;
  color: #ffffff;
  width: 100%;
}

.form h2 {
  font-family: "Alumni Sans", "Arial", sans-serif;
  font-size: 54px;
  font-weight: 800;
  letter-spacing: 0.03em;
  line-height: 156%;
  margin: 0;
  margin-bottom: 10px;
}
@media (max-width: 1199px) {
  .form h2 {
    font-size: 42px;
    font-weight: 700;
    line-height: 50px;
    margin-bottom: 27px;
  }
}
@media (max-width: 767px) {
  .form h2 {
    font-size: 32px;
    font-weight: 600;
    line-height: 38px;
  }
}

.form > .container {
  display: flex;
  flex-direction: column;
  height: 100%;
  justify-content: center;
  margin-left: auto;
  margin-right: 0;
  max-width: 683px;
  padding: 56px 170px 70px 103px;
  width: 100%;
}
@media (max-width: 1199px) {
  .form > .container {
    margin: 0;
    max-width: 100%;
    padding: 61px 162px 70px 133px;
  }
}
@media (max-width: 767px) {
  .form > .container {
    padding: 64px 15px 69px 15px;
  }
}

.form form {
  column-gap: 30px;
  display: grid;
  grid-template-columns: 1fr 1fr;
}
@media (max-width: 1199px) {
  .form form {
    column-gap: 60px;
  }
}
@media (max-width: 767px) {
  .form form {
    display: flex;
    flex-direction: column;
  }
}

.form form button[type=submit] {
  background-color: transparent;
  border: 0;
  color: #ffffff;
  font-size: 14px;
  font-weight: 700;
  grid-column: 1/-1;
  letter-spacing: 0.05em;
  line-height: 16px;
  margin: 0;
  min-height: 54px;
  padding: 22px 20px 17px;
  position: relative;
  text-transform: uppercase;
  transition: 0.3s color;
  z-index: 0;
}
@media (max-width: 767px) {
  .form form button[type=submit] {
    margin-top: 15px;
  }
}
.form form button[type=submit]::before {
  background-color: #ffffff;
  content: "";
  height: 100%;
  left: 0;
  position: absolute;
  top: 0;
  transition: 0.3s clip-path;
  width: 100%;
  z-index: -1;
  clip-path: polygon(0% 0%, 0% 100%, 3px 100%, 3px 3px, calc(100% - 3px) 3px, calc(100% - 3px) calc(100% - 3px), 3px calc(100% - 3px), 3px 100%, 100% 100%, 100% 0%);
}
@media (hover: hover) {
  .form form button[type=submit]:hover:not(.focus-visible) {
    color: #102654;
  }
  .form form button[type=submit]:hover:not(.focus-visible)::before {
    clip-path: polygon(0% 0%, 0% 100%, 3px 100%, 3px 3px, 3px 3px, 3px calc(100% - 3px), 3px calc(100% - 3px), 3px 100%, 100% 100%, 100% 0%);
  }
}
.form form button[type=submit].focus-visible:focus {
  color: #102654;
}
.form form button[type=submit].focus-visible:focus::before {
  clip-path: polygon(0% 0%, 0% 100%, 3px 100%, 3px 3px, 3px 3px, 3px calc(100% - 3px), 3px calc(100% - 3px), 3px 100%, 100% 100%, 100% 0%);
}

